import { APP_NAME } from "./common";
import { Team, getTeam } from "../../stores/cost/TeamsStoreAdapter";

import { Term, getTerms } from "../../stores/cost/TermsStoreAdapter";
import { Breadcrumb } from "@toolpad/core";

export const RouterTitle = (pathname: string) => {
  // const activePage = useActivePage();
  // console.log(activePage);

  if (isHome(pathname)) {
    return "Ardito Cost Management System";
  }
  if (isPartner(pathname)) {
    return "";
  }
  if (isMember(pathname)) {
    return "";
  }

  return undefined;
};

export const RouterBreadcrumbs = (
  pathname: string,
  teams: { [key: string]: Team },
  terms: Term[]
) => {
  if (isHome(pathname)) {
    return [{ title: "HOME", path: `${APP_NAME}/cost` }];
  }
  if (isPartner(pathname) || isMember(pathname)) {
    return [
      { title: "HOME", path: `${APP_NAME}/cost` },
      { title: "", path: `${APP_NAME}/cost/mgr` },
    ];
  }

  const teamParam = matchPath(`/${APP_NAME}/cost/team/:managerId/:teamId`, pathname);
  if (teamParam && teamParam.length == 3) {
    const teamId = teamParam[2];
    return extractTeamBreadcrumbs(teamId, teams, terms);
  }

  return undefined;
};

const extractTeamBreadcrumbs = (
  teamId: string,
  teams: { [key: string]: Team },
  terms: Term[]
): Breadcrumb[] => {
  const team = teams[teamId];
  const term = terms.find((e) => e.termNumber === team.termNumber)!;
  return [
    { title: "HOME", path: `${APP_NAME}/cost` },
    { title: team.manager.name, path: `${APP_NAME}/team/${team.managerId}` },
    {
      title: `${term.name}(${term.yearFrom}-${term.yearTo})`,
      path: `${APP_NAME}/team/${team.managerId}?termNumber=${team.termNumber}`,
    },
  ];
};

export const root = `/${APP_NAME}/cost`;

const isHome = (pathname: string) => pathname === root;

const isPartner = (pathname: string) => pathname === `${root}/partner`;

const isMember = (pathname: string) => pathname === `${root}/member`;

function matchPath(pattern: string, pathname: string): string[] | null {
  const regex = new RegExp(`^${pattern.replace(/:[^/]+/g, "([^/]+)")}$`);
  console.log(regex);
  const match = pathname.match(regex);
  console.log(match);
  return match ? match : null;
}
