import React, { useState, useEffect } from "react";

import { useParams } from "react-router-dom";

import { Stack, Grid, IconButton, Divider } from "@mui/material";
import Typography from "@mui/material/Typography";
import Box from "@mui/material/Box";
import TextField from "@mui/material/TextField";
import Collapse from "@mui/material/Collapse";
import Paper from "@mui/material/Paper";
import Table from "@mui/material/Table";
import TableBody from "@mui/material/TableBody";
import TableCell from "@mui/material/TableCell";
import TableContainer from "@mui/material/TableContainer";
import TableHead from "@mui/material/TableHead";
import TableRow from "@mui/material/TableRow";
import Tooltip from "@mui/material/Tooltip";

import KeyboardArrowDownIcon from "@mui/icons-material/KeyboardArrowDown";
import KeyboardArrowUpIcon from "@mui/icons-material/KeyboardArrowUp";
import PersonAddIcon from "@mui/icons-material/PersonAdd";

import TeamMemberEdit from "./components/TeamMemberAdd";

import SubWindow from "../../components/ SubWindow";
import Circular from "../../components/CustomCircular";

import { successDefault, errorDefault } from "../../utils/Toastify";

import {
  // api
  getMonths,
  // model
  Month,
} from "../../stores/cost/MonthsStoreAdapter";

import {
  // api
  createMember,
  getTeamMembers,
  // model
  TeamMember,
} from "../../stores/cost/TeamMembersStoreAdapter";

import {
  // api
  updateResults,
  // model
  Result,
} from "../../stores/cost/ResutsStoreAdapter";

const TeamPage: React.FC = () => {
  const { teamId } = useParams();

  const [update, setUpdate] = useState(true);

  const [isProgress, setProgress] = useState(true);

  const [isAddOpen, setAddOpen] = useState(false);

  const [months, setMonths] = useState<Month[]>([]);
  const [teamMembers, setTeamMembers] = useState<TeamMember[]>([]);

  useEffect(() => {
    setProgress(true);
    Promise.all([getMonths(), getTeamMembers(teamId!)])
      .then((results) => {
        setMonths(results[0]);
        setTeamMembers(results[1]);
        setProgress(false);
      })
      .catch((e: Error) => {
        errorDefault("!!");
        setProgress(false);
      });
  }, [teamId, update]);

  const handleAddOpen = () => {
    setAddOpen(true);
  };

  const handleAddClose = () => {
    setAddOpen(false);
  };

  const updateRow = (teamMember: TeamMember) => {
    const newTeamMembers = teamMembers.map((e) => {
      if (e.teamMemberId! === teamMember.teamMemberId!) {
        return teamMember;
      }
      return e;
    });

    setTeamMembers(newTeamMembers);
  };

  return (
    <>
      <Stack spacing={5} divider={<Divider flexItem />}>
        <Paper sx={{ width: "100%" }}>
          <TableContainer sx={{ maxHeight: 700 }}>
            <Table stickyHeader aria-label="table">
              <TableHead>
                <TableRow>
                  <TableCell
                    align="center"
                    colSpan={2}
                    style={{
                      minWidth: "200px",
                      height: "40px",
                      borderRight: "1px solid grey",
                      borderBottom: "none",
                    }}
                  ></TableCell>
                  {months
                    .sort((c1, c2) => c1.order - c2.order)
                    .map((e) => {
                      return (
                        <TableCell
                          key={`header-col${e.month}`}
                          align="center"
                          colSpan={2}
                          style={{
                            padding: "1px",
                            height: "40px",
                            borderTop: "1px solid grey",
                            borderRight: "2px solid grey",
                            borderBottom: "2px solid grey",
                          }}
                        >
                          <Typography variant="h6" sx={{ fontWeight: "bold", m: 1 }}>
                            {e.name}
                          </Typography>
                        </TableCell>
                      );
                    })}
                </TableRow>
                <TableRow style={{ maxHeight: "5px", padding: "1px" }}>
                  <TableCell
                    align="center"
                    colSpan={2}
                    style={{
                      top: "52px",
                      minWidth: "200px",
                      padding: "1px",
                      borderTop: "none",
                      borderRight: "1px solid grey",
                    }}
                  >
                    <IconButton onClick={() => handleAddOpen()} style={{ padding: "5px" }}>
                      <Tooltip title="">
                        <PersonAddIcon fontSize="medium" />
                      </Tooltip>
                    </IconButton>
                  </TableCell>
                  {months
                    .sort((c1, c2) => c1.order - c2.order)
                    .map((e) => {
                      return (
                        <>
                          <TableCell
                            align="center"
                            style={{
                              top: "52px",
                              padding: "1px",
                              borderRight: "1px solid grey",
                            }}
                          >
                            
                          </TableCell>
                          <TableCell
                            align="center"
                            style={{
                              top: "52px",
                              padding: "1px",
                              borderRight: "2px double grey",
                            }}
                          >
                            
                          </TableCell>
                        </>
                      );
                    })}
                </TableRow>
              </TableHead>
              <TableBody>
                {teamMembers.map((t) => (
                  <Row
                    key={t.teamMemberId!}
                    teamMember={t}
                    callBack={updateRow}
                    months={months}
                    setProgress={setProgress}
                  />
                ))}
              </TableBody>
            </Table>
          </TableContainer>
        </Paper>
      </Stack>
      <SubWindow title="" open={isAddOpen} handleClose={handleAddClose}>
        <TeamMemberEdit
          mode="c"
          teamId={teamId!}
          handleUpdate={() => setUpdate(!update)}
          handleClose={handleAddClose}
          setProgress={setProgress}
        />
      </SubWindow>
      <Circular open={isProgress} />
    </>
  );
};

function Row(props: { teamMember: TeamMember; callBack: any; months: Month[]; setProgress: any }) {
  const { teamMember, months } = props;
  const [open, setOpen] = React.useState(false);

  const updateCell = (r: Result, c: number, s: number) => {
    props.setProgress(true);
    r.cost = c;
    r.sales = s;

    updateResults(teamMember.teamMemberId!, [r])
      .then((result: void) => {
        const newResults = teamMember.results.map((e) => {
          if (e.month === r.month) {
            return r;
          }
          return e;
        });
        teamMember.results = newResults;
        props.callBack(teamMember);
        props.setProgress(false);
        successDefault("");
      })
      .catch((e: Error) => {
        props.setProgress(false);
        errorDefault("!!");
      });
  };

  return (
    <React.Fragment>
      <TableRow sx={{ "& > *": { borderBottom: "unset" } }}>
        <TableCell style={{ padding: "1px", borderTop: "1px solid grey", borderBottom: "none" }}>
          <IconButton aria-label="expand row" size="small" onClick={() => setOpen(!open)}>
            {open ? (
              <Tooltip title="">
                <KeyboardArrowUpIcon color="primary" />
              </Tooltip>
            ) : (
              <Tooltip title="">
                <KeyboardArrowDownIcon color="primary" />
              </Tooltip>
            )}
          </IconButton>
        </TableCell>
        <TableCell
          component="th"
          scope="row"
          style={{
            padding: "1px",
            borderTop: "1px solid grey",
            borderBottom: "none",
            borderRight: "1px solid grey",
          }}
        >
          <Typography sx={{ m: 1 }}>{teamMember.member!.name}</Typography>
        </TableCell>
        {months
          .sort((c1, c2) => c1.order - c2.order)
          .map((e) => {
            const r = teamMember.results[e.order - 1];
            return (
              <>
                <CostCell
                  cost={r.cost}
                  callBack={(v: number) => {
                    updateCell(r, v, r.sales);
                  }}
                />
                <SalesCell
                  sales={r.sales}
                  callBack={(v: number) => {
                    updateCell(r, r.cost, v);
                  }}
                />
              </>
            );
          })}
      </TableRow>
      <TableRow>
        <TableCell style={{ paddingBottom: 0, paddingTop: 0, borderBottom: "none" }} colSpan={6}>
          <Collapse in={open} timeout="auto" unmountOnExit>
            <Box sx={{ padding: 1 }}>
              <Typography
                variant="h5"
                gutterBottom
                component="div"
                sx={{ fontWeight: "bold", m: 1 }}
              >
                
              </Typography>
              <Table size="small" stickyHeader aria-label="purchases">
                <TableHead>
                  <TableRow>
                    <TableCell align="left" style={{ top: "80px", zIndex: 1 }}>
                      
                    </TableCell>
                    <TableCell align="left" style={{ top: "80px", zIndex: 1 }}>
                      
                    </TableCell>
                    <TableCell align="left" style={{ top: "80px", zIndex: 1 }}>
                      
                    </TableCell>
                    <TableCell align="left" style={{ top: "80px", zIndex: 1 }}>
                      
                    </TableCell>
                  </TableRow>
                </TableHead>
                <TableBody>
                  {months
                    .sort((c1, c2) => c1.order - c2.order)
                    .map((e, i) => {
                      const r = teamMember.results[e.order - 1];
                      return (
                        <>
                          <TableRow key={`${teamMember.teamMemberId}-${i}`}>
                            <TableCell>{e.name}</TableCell>
                            <TableCell>{r.cost}</TableCell>
                            <TableCell>{r.sales}</TableCell>
                            <TableCell>{r.sales - r.cost}</TableCell>
                          </TableRow>
                        </>
                      );
                    })}
                  <TableRow
                    key={`${teamMember.teamMemberId}-total`}
                    style={{ backgroundColor: "#191970" }}
                  >
                    <TableCell></TableCell>
                    <TableCell>{sum(teamMember.results.map((e) => e.cost))}</TableCell>
                    <TableCell>{sum(teamMember.results.map((e) => e.sales))}</TableCell>
                    <TableCell>{sum(teamMember.results.map((e) => e.sales - e.cost))}</TableCell>
                  </TableRow>
                </TableBody>
              </Table>
            </Box>
          </Collapse>
        </TableCell>
      </TableRow>
    </React.Fragment>
  );
}

function sum(numbers: number[]) {
  return numbers.reduce((a, c) => {
    return a + c;
  }, 0);
}

function CostCell(props: { cost: string | number; callBack: any }) {
  const [value, setValue] = useState(props.cost);
  const [isEditable, setEditable] = useState(false);
  return (
    <React.Fragment>
      <TableCell
        align="left"
        style={{
          minWidth: "100px",
          padding: "1px",
          borderTop: "1px solid grey",
          borderRight: "1px solid grey",
        }}
      >
        {isEditable && value !== "-" ? (
          <TextField
            autoFocus
            fullWidth
            onBlur={() => {
              if ("" === value) {
                setValue(0);
              }
              props.callBack(value);
              setEditable(!isEditable);
            }}
            onKeyDown={(e) => {
              if ("Enter" !== e.key) {
                return;
              }
              if ("" === value) {
                setValue(0);
              }
              props.callBack(value);
              setEditable(!isEditable);
            }}
            type="number"
            value={value}
            onChange={(e) => {
              setValue(Number(e.target.value));
            }}
            variant="standard"
            style={{
              paddingLeft: "5px",
              paddingRight: "5px",
            }}
            slotProps={{
              inputLabel: {
                shrink: true,
              },
            }}
          />
        ) : (
          <Typography
            sx={{ fontWeight: "bold", m: 1, color: "#FFBEDA" }}
            onClick={() => {
              if (0 === value) {
                setValue("");
              }
              setEditable(!isEditable);
            }}
          >
            {value}
          </Typography>
        )}
      </TableCell>
    </React.Fragment>
  );
}

function SalesCell(props: { sales: string | number; callBack: any }) {
  const [value, setValue] = useState(props.sales);
  const [isEditable, setEditable] = useState(false);
  return (
    <React.Fragment>
      <TableCell
        align="left"
        style={{
          minWidth: "100px",
          padding: "1px",
          borderTop: "1px solid grey",
          borderRight: "2px double grey",
        }}
      >
        {isEditable && value !== "-" ? (
          <TextField
            autoFocus
            fullWidth
            onBlur={() => {
              if ("" === value) {
                setValue(0);
              }
              props.callBack(value);
              setEditable(!isEditable);
            }}
            onKeyDown={(e) => {
              if ("Enter" !== e.key) {
                return;
              }
              if ("" === value) {
                setValue(0);
              }
              props.callBack(value);
              setEditable(!isEditable);
            }}
            type="number"
            value={value}
            onChange={(e) => {
              setValue(Number(e.target.value));
            }}
            variant="standard"
            style={{
              paddingLeft: "5px",
              paddingRight: "5px",
            }}
            slotProps={{
              inputLabel: {
                shrink: true,
              },
            }}
          />
        ) : (
          <Typography
            sx={{ fontWeight: "bold", m: 1, color: "#CBFFD3" }}
            onClick={() => {
              if (0 === value) {
                setValue("");
              }
              setEditable(!isEditable);
            }}
          >
            {value}
          </Typography>
        )}
      </TableCell>
    </React.Fragment>
  );
}

export default TeamPage;
