import { BrowserRouter, Route, Routes } from "react-router-dom";

import { ToastContainer } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";

import CostRoute from "./routes/CostRoute";
import CostIndex from "./pages/cost/Index";

import Partner from "./pages/cost/Partner";
import Member from "./pages/cost/Member";
import Team from "./pages/cost/Team";
import Team2 from "./pages/cost/Team2";

import { APP_NAME } from "./layouts/router/common";

function App() {
  return (
    <>
      <BrowserRouter>
        <Routes>
          <Route path={`/${APP_NAME}`} element={<>Home</>} />

          <Route path={`/${APP_NAME}/cost`} Component={CostRoute}>
            <Route index Component={CostIndex} />
            <Route path={`/${APP_NAME}/cost/partner`} Component={Partner} />
            <Route path={`/${APP_NAME}/cost/member`} Component={Member} />
            <Route path={`/${APP_NAME}/cost/team/:managerId/:teamId`} Component={Team2} />
            <Route path={`/${APP_NAME}/cost/team2/:managerId/:teamId`} Component={Team} />
            <Route path={`/${APP_NAME}/cost/*`} element={<></>} />
          </Route>
        </Routes>
      </BrowserRouter>
      <ToastContainer
        position="top-right"
        autoClose={2000}
        hideProgressBar={false}
        newestOnTop={false}
        closeOnClick
        rtl={false}
        pauseOnFocusLoss
        draggable
        pauseOnHover
      />
    </>
  );
}

export default App;
