import { generateUUID, getFormattedDateTime } from "../../utils/Common";

import { Manager } from "./ManagerStoreAdapter";

export type Team = {
  teamId: string;
  manager: Manager;
  managerId: string;
  termNumber: number;
  name: string;
  note?: string;
  createdAt?: string;
  createdBy?: string;
  updatedAt?: string;
  updatedBy?: string;
};

