import React, { useState, useEffect } from "react";
import { useNavigate } from "react-router-dom";

import { createTheme } from "@mui/material/styles";
import Stack from "@mui/material/Stack";
import Chip from "@mui/material/Chip";
import Typography from "@mui/material/Typography";

import { AppProvider, NavigationItem, Router } from "@toolpad/core/AppProvider";
import { PageContainer } from "@toolpad/core/PageContainer";
import { DashboardLayout } from "@toolpad/core/DashboardLayout";

import PaymentIcon from "@mui/icons-material/Payment";
import HandshakeIcon from "@mui/icons-material/Handshake";
import PeopleAltIcon from "@mui/icons-material/PeopleAlt";
import CalendarMonthIcon from "@mui/icons-material/CalendarMonth";
import CurrencyYenIcon from "@mui/icons-material/CurrencyYen";
import { Outlet } from "react-router-dom";

import { errorDefault } from "../utils/Toastify";

import {
  // api
  getTeams,
  // model
  Team,
} from "../stores/cost/TeamsStoreAdapter";

import {
  // api
  getTerms,
  Term,
} from "../stores/cost/TermsStoreAdapter";

import { APP_NAME } from "./router/common";
import { RouterTitle, RouterBreadcrumbs } from "./router/cost";
import { Manager } from "../stores/cost/ManagerStoreAdapter";

const CostLayout: React.FC = () => {
  const [navigation, setNavigation] = useState<NavigationItem[]>([]);

  const [terms, setTerms] = useState<Term[]>([]);
  const [teams, setTeams] = useState<{ [key: string]: Team }>({});

  useEffect(() => {
    Promise.all([getTerms(), getTeams()])
      .then((results) => {
        setTerms(results[0]);
        const ts: { [key: string]: Team } = {};

        const mtg: { [key: string]: { [key: number]: Team[] } } = {};
        for (const t of results[1]) {
          const mid = t.managerId;
          if (!mtg[mid]) {
            mtg[mid] = {};
          }

          const tg = mtg[mid];

          const tn = t.termNumber;
          if (!tg[tn]) {
            tg[tn] = [];
          }
          tg[tn].push(t);
          ts[t.teamId] = t;
        }
        setTeams(ts);

        const items: NavigationItem[] = [];
        items.push({ kind: "header", title: "" });
        items.push({
          segment: `${APP_NAME}/cost/partner`,
          title: "",
          icon: <HandshakeIcon />,
        });
        items.push({ segment: `${APP_NAME}/cost/member`, title: "", icon: <PeopleAltIcon /> });

        let manager: Manager | null = null;
        for (const tg of Object.values(mtg)) {
          if (!manager) {
            manager = Object.values(tg)[0]![0]!.manager!;
            items.push({ kind: "divider" });
            items.push({ kind: "header", title: manager.name });
          }

          for (const term of results[0]) {
            const item: NavigationItem = {
              segment: `${APP_NAME}/cost/team/${manager.managerId}`,
              title: `${term.name}(${term.yearFrom}-${term.yearTo})`,
              icon: <CalendarMonthIcon />,
              children: [],
            };

            const children: NavigationItem[] = [];
            for (const team of tg[term.termNumber]) {
              const child: NavigationItem = {
                segment: `${team.teamId}`,
                title: team.name,
                icon: <CurrencyYenIcon />,
              };
              children.push(child);
            }
            item.children = children;
            items.push(item);
          }
          manager = null;
        }
        setNavigation(items);
      })
      .catch((e: Error) => {
        console.error(e);
        errorDefault("!!");
      });
  }, []);

  const router = useRouter(`/${APP_NAME}/cost`);

  const breadcrumbs = React.useMemo(() => {
    return RouterBreadcrumbs(router.pathname, teams, terms);
  }, [router.pathname]);

  const title = React.useMemo(() => {
    return RouterTitle(router.pathname);
  }, [router.pathname]);

  return (
    <AppProvider navigation={navigation} router={router} theme={theme}>
      <DashboardLayout
        slots={{
          appTitle: CustomAppTitle,
        }}
      >
        <PageContainer maxWidth="xl" title={title} breadcrumbs={breadcrumbs}>
          <Outlet />
        </PageContainer>
      </DashboardLayout>
    </AppProvider>
  );
};

function useRouter(initialPath: string): Router {
  const navigate = useNavigate();
  const [pathname, setPathname] = React.useState(initialPath);

  const router = React.useMemo(() => {
    return {
      pathname,
      searchParams: new URLSearchParams(),
      navigate: (path: string | URL) => {
        navigate(String(path));
        setPathname(String(path));
      },
    };
  }, [pathname]);

  return router;
}

const theme = createTheme({
  colorSchemes: { light: true, dark: true },
  cssVariables: {
    colorSchemeSelector: "class",
  },
  breakpoints: {
    values: {
      xs: 0,
      sm: 600,
      md: 600,
      lg: 1200,
      xl: 1536,
    },
  },
});

function CustomAppTitle() {
  return (
    <Stack direction="row" alignItems="center" spacing={2}>
      <PaymentIcon fontSize="large" color="primary" />
      <Typography color="primary" variant="h6" sx={{ fontWeight: "bold", m: 1 }}>
        Ardito Cost Management Syster
      </Typography>
      <Chip size="small" label="BETA" color="warning" />
    </Stack>
  );
}

export default CostLayout;
